	// å¼€å§‹è½®è¯¢æ–‡ä»¶çŠ¶æ€
	function startPollingFileStatus(projectId) {
		// å¦‚æœæ²¡æœ‰æ–‡ä»¶åœ¨å¤„ç†ï¼Œä¸éœ€è¦è½®è¯¢
		if (!hasProcessingFiles()) {
			// console.log('âœ… æ‰€æœ‰æ–‡ä»¶å·²å¤„ç†å®Œæˆï¼Œæ— éœ€è½®è¯¢')
			return
		}

		// é˜²æ­¢é‡å¤è½®è¯¢
		if (isPolling.value) {
			console.log('âš ï¸ å·²åœ¨è½®è¯¢ä¸­ï¼Œè·³è¿‡')
			return
		}

		isPolling.value = true
		pollingCount.value = 0

		const processingCount = projectFiles.value.filter(f =>
			!['indexed', 'failed'].includes(f.status)
		).length

		// console.log(`ğŸ”„ å¼€å§‹è½®è¯¢é¡¹ç›® ${projectId} çš„æ–‡ä»¶çŠ¶æ€...`)
		// console.log(`   è¿˜æœ‰ ${processingCount} ä¸ªæ–‡ä»¶æ­£åœ¨å¤„ç†`)

		pollingTimer.value = setInterval(async () => {
			pollingCount.value++

			// è¶…è¿‡æœ€å¤§æ¬¡æ•°ï¼Œåœæ­¢è½®è¯¢
			if (pollingCount.value > MAX_POLLING_COUNT) {
				console.log('â° è¾¾åˆ°æœ€å¤§è½®è¯¢æ¬¡æ•°ï¼Œåœæ­¢è½®è¯¢')
				stopPolling()
				uni.showToast({
					title: 'æ–‡ä»¶å¤„ç†è¶…æ—¶ï¼Œè¯·æ‰‹åŠ¨åˆ·æ–°',
					icon: 'none',
					duration: 2000
				})
				return
			}

			try {
				// é™é»˜é‡æ–°åŠ è½½æ–‡ä»¶åˆ—è¡¨ï¼ˆä¸æ˜¾ç¤º loadingï¼‰
				await loadProjectFiles(projectId, true)

				// æ£€æŸ¥æ˜¯å¦æ‰€æœ‰æ–‡ä»¶éƒ½å¤„ç†å®Œæˆ
				if (!hasProcessingFiles()) {
					console.log('âœ… æ‰€æœ‰æ–‡ä»¶å¤„ç†å®Œæˆï¼Œåœæ­¢è½®è¯¢')
					stopPolling()

					// æ˜¾ç¤ºæˆåŠŸæç¤º
					uni.showToast({
						title: 'æ–‡ä»¶å¤„ç†å®Œæˆ',
						icon: 'success',
						duration: 2000
					})
				} else {
					// ç»Ÿè®¡å¤„ç†ä¸­çš„æ–‡ä»¶
					const processing = projectFiles.value.filter(f =>
						!['indexed', 'failed'].includes(f.status)
					)
					// console.log(`ğŸ”„ [è½®è¯¢ ${pollingCount.value}] è¿˜æœ‰ ${processing.length} ä¸ªæ–‡ä»¶æ­£åœ¨å¤„ç†...`)
				}
			} catch (error) {
				console.error('âŒ è½®è¯¢æ–‡ä»¶çŠ¶æ€å¤±è´¥:', error)
				// ä¸åœæ­¢è½®è¯¢ï¼Œç»§ç»­å°è¯•
			}
		}, POLLING_INTERVAL)
	}

	// åœæ­¢è½®è¯¢
	function stopPolling() {
		if (pollingTimer.value) {
			clearInterval(pollingTimer.value)
			pollingTimer.value = null
