		})

		try {
			// #ifdef H5
			// H5ç¯å¢ƒï¼šä½¿ç”¨aæ ‡ç­¾ä¸‹è½½
			await downloadFileH5(url, filename)
			// #endif

			// #ifdef MP-WEIXIN
			// å¾®ä¿¡å°ç¨‹åºç¯å¢ƒ
			await downloadFileWechat(url, filename)
			// #endif

