			`ç¡®è®¤è¦åˆ é™¤ã€Œ${names[0]}ã€å—ï¼Ÿ` :
			(() => {
				const max = 8
				const head = names.slice(0, max).map(n => `â€¢ ${n}`).join('\n')
				const tail = names.length > max ? `\nâ€¦ ç­‰ ${names.length} é¡¹` : ''
				return `ç¡®è®¤è¦åˆ é™¤ä»¥ä¸‹ ${names.length} ä¸ªä¿¡æ¯å—ï¼Ÿ\n${head}${tail}`
			})()

		uni.showModal({
